/* Global CSS */

body {
  margin: 0;
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: "Roboto", sans-serif;
  -moz-osx-font-smoothing: "Roboto", sans-serif;
  font-style: normal;
}
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}
:root {
  --primary: DarkSlateGray;
  --heading-color: #282938;
  --bg-shade: #f5fcff;
  --github: #e62872;
  --darkblue: #1c1e53;
  --black: #000000;
  --white: #ffffff;
 
}
.btn {
  font-family: "Roboto";
  display: inline-block;
  padding: 14px 32px;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 10px;
  cursor: pointer;
  text-align: center;
  text-decoration: none;
  border-radius: 35px;
  -webkit-border-radius: 35px;
  -moz-border-radius: 35px;
  -ms-border-radius: 35px;
  -o-border-radius: 35px;
  transition: background-color 0.5s;
  -webkit-transition: background-color 0.5s;
  -moz-transition: background-color 0.5s;
  -ms-transition: background-color 0.5s;
  -o-transition: background-color 0.5s;
}
.btn-outline-primary {
  color: white;
  background-color: rgb(80, 125, 124);
  border: 1px solid rgb(80, 125, 124);
}
.btn-outline-primary:hover {
  color: var(--white);
  background-color: rgb(45, 156, 130);
}
.btn-primary {
  color: var(--white);
   background-color: rgb(45, 156, 130);
  
}
.btn-primary:hover {
  color: rgb(78, 194, 174);
  background-color:rgb(49, 89, 88);
}
.btn-github {
  color: var(--white);
  background-color: var(--github);
  border: btn-primary;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  box-shadow: 0 1px 2px 0 rgba(16, 24, 40, 0.95);
  gap: 16px;
}
.btn-github:hover {
  color: var(--github);
  background-color: var(--white);
}
/* Global CSS Ends */
/* Main Headings */

/* Heading 1 */
h1 {
  font-size: 56px;
  font-weight: 700;
  line-height: 67px;
}
/* Heading 2 */
h2 {
  font-size: 48px;
  font-weight: 700;
  line-height: 58px;
}
/* Heading 3 */
h2 {
  font-size: 24px;
  font-weight: 700;
  line-height: 34px;
}

/* Pragraphs text large medium small */
/* Body 1 */
.text-lg {
  color: var(--darkblue);
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 27px;
}
/* Body 2 */
.text-md {
  color: var(--darkblue);
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
}
/* Body 3 */
.text-sm {
  color: var(--black);
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 21px;
}

/* Section Titles */

.section--title {
  color: var(--heading-color);
  font-size: 21px;
  text-align: center;
  font-style: normal;
  font-weight: 600;
  line-height: 32px;
  display: flex;
  align-items: flex-start;
}
/* Portfolio, Testimonial & Contact Me */

.sub--title {
  color: var(--heading-color);
  font-size: 16px;
  font-weight: 600;
  line-height: 24px;
}
/* Section Titles Ends */

/* Navbar Style Start */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 85.333px;
  background-color:rgb(62,104,104);
  color:white;
  box-shadow: 0px 5.333px 80px 0 rgba(0, 0, 0, 0.1);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
 } 
  
.circle-container{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 70px; 
  height: 70px;
  border-radius: 50%; /* Make it a circle */
  border:1px solid lightgray;
  box-shadow: 0 4px 8px rgba(0,0,0,.1);
  font-size:40px;
  font-weight:bold;
  margin-left:-30px;
  
  
}

.logosize{
  width: 70px; 
  height: 70px;

}


.navbar--items > ul {
  list-style: none;
  display: flex;
  align-items: flex-start;
  gap: 42.667px;
  text-decoration: none;
  padding-inline-start: 0;
}
.navbar--items ul > li > a {
  text-decoration: none;
}
/* Navbar Content */

.navbar--active-content {
   color:rgb(120, 228, 211);
   text-decoration: underline !important;
}
.navbar--link {
  text-decoration: none;
  
}

.navbar--link:hover {
  text-decoration: underline; /* Add underline on hover */
}

.logosize{
  width: 70px; 
  height: 70px;

}

.nav-name{
font-family: "Roboto", sans-serif;
color: white;
}
/* Navbar Styler Ends */




