.homepage-section {
  position: relative;
  min-height:970px;
  display: flex;
  justify-content: start; /* Center content horizontally */
  align-items: center;
 
   
}

.background-image {
  position: absolute;
  background-color:rgb(62,104,104);
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover; /* Ensures the image covers the entire section */
  z-index: 1; /* Places the image behind other content */
}
.homeContent {
  position: relative;
  z-index: 2; 
  margin:0 85px 0 85px;
  padding:0 85px 0 85px; 
  display:flex;
  flex-direction:column;
  align-items: start; 
  font-family: "Dosis", sans-serif;
  gap:10px;
  color:white;
 
  /* Places the content on top of the image */
}
.home_name{
font-size:55px;
 font-family: "Dosis", sans-serif;
  font-optical-sizing: auto;
  font-weight: 600; /* Use the desired font weight */
  font-style: normal;
 
 
}
.homeContent p{
 font-size:30px;
 font-family: "Dosis", sans-serif;
  font-optical-sizing: auto;
  font-weight: 500; /* Use the desired font weight */
  font-style: normal;
 
}
.media-icons{
 display:flex;
 padding-inline-start: 0;
 gap:20px;
 margin-top:0px;
 margin-bottom:0px;
}

.media-icons li{
  list-style: none;

}

.home-navbar--items ul{
 display:flex;
 flex-direction:row;
 gap:35px;
  padding-inline-start: 0;
}
.home-navbar--items ul li{
  list-style: none;
  font-size:20px;
}

.firstlink{
text-decoration: underline;
 color:rgb(120, 228, 211);
}

/* Hero Section Style */






/* My Portfolio Starts */
.portfolio--section {
margin-bottom:150px;
  display: flex;
  padding: 112px 85px;  
  flex-direction: column;
  align-items: flex-start;
  gap: 80px;
  
}
.portfolio--section--container {
  display: flex;
 
  gap: 10px;
  justify-content:center;
  align-items: stretch;
  flex:1;
}




.portfolio--section--card {
  display: flex;
  flex-direction: column;
   background-color:var(--bg-shade);
  box-shadow: 0 0 32px 0 rgba(0, 0, 0, 0.15);
  cursor: pointer;
  width:100%;
  
}
.portfolio--section--img{
  box-shadow: 0 0 32px 0  rgba(0, 0, 0, 0.15);
  height: 200px; 
}

.portfolio--section--img > img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
}
.portfolio--section--img:hover > img {
  transform: scale(0.9);
}
.discriptionBoxSize{ 
  height: 200px;
}
.portfolio--section--card--content {
  display: flex;
  padding: 32px;
  flex-direction: column;
  align-items: flex-start;
  gap: 32px;
 
}
.portfolio--section--title {
  color: var(--heading-color);
  margin-bottom:20px;
}
.portfolio--link {
  text-decoration: none;
  display: flex;
  gap: 16px;
  border-bottom: 1.33px solid var(--primary);
  padding-bottom: 10.6px;
  font-weight: 600;
  
}


/* Portfolio Ends */

/*hero section*/
.hero--section {

  display: grid;
  height:100%;
  grid-template-columns: repeat(2, 1fr);
  padding: 133.333px 85.333px 133.333px;
  align-items: center;
  justify-content: space-between;
  gap: 60px;
  background-color: rgba(255, 255, 255, 0.9); 
  background-color:var(--bg-shade);
}

.hero--title{
  color: var(--heading-color);
  font-size: 16px;
  font-weight: 600;
  line-height: 24px;
  margin-bottom:20px;
}


.hero--section--content-box {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 32px;

}
.hero--section--content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 21.333px;
}

.hero--section--content--box > button {
  margin-top: 21.333px;
}
.hero--section--title {
  color: var(--heading-color);
  font-size: 74.667px;
  font-weight: 700;
  line-height: 90px;
  align-self: stretch;
  margin-bottom:20px;
}
.hero--section--title--color {
  color: var(--primary);
}
.hero--section-description {
  color: var(--darkblue);
  font-size: 24px;
  font-weight: 400;
  line-height: 36px;
  margin-bottom:20px;
}
.hero--section--img {
  display: flex;
 justify-content: center;
 align-items: center;

}
.hero--section--img > img {
    max-width:100%; 
    height: auto;
    border-radius: 5px; 
    
}
/* Hero Section Style Ends */





